@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&family=Kanit:wght@200;300;400;500;600;700;800&family=Work+Sans:wght@200;300;400;500;600;700;800&display=swap");
body, * {
  box-sizing: border-box;
  margin: 0;
}

body {
  padding: 13px;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1rem;
}

/* COLORS */
.main_page {
  background-color: #8B4FB3;
  padding: 20px 0 30px 0;
}

.scrollUpBtn {
  cursor: pointer;
  transition: 0.1s ease-in-out;
}
.scrollUpBtn:hover {
  background-color: #8B4FB3;
}
.scrollUpBtn:hover svg {
  color: #fff;
}

@media screen and (min-width: 550px) {
  .main_page-content {
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(380px, 380px));
    gap: 0 35px;
  }
}
.form {
  transform: translateX(110%);
  position: fixed;
  text-align: end;
  right: 0;
  top: 40px;
  transition: 0.4s ease-out;
  z-index: 5000;
}
.form.active {
  transform: translatex(0);
}
.form h2 {
  padding: 7px;
  background: #fff;
  color: #8B4FB3;
  box-shadow: -9px 13px 18px 4px rgba(0, 0, 0, 0.75);
  cursor: pointer;
}
.form .input {
  padding: 7px;
  color: #8B4FB3;
  box-shadow: -9px 13px 18px 4px rgba(0, 0, 0, 0.75);
  background: #fff;
}
.form .input input {
  border: none;
  height: 35px;
  width: 242px;
  outline: none;
  cursor: pointer;
}
.form .btn button {
  font-size: 1rem;
  cursor: pointer;
  margin-top: 15px;
  border: none;
  box-shadow: -9px 13px 18px 4px rgba(0, 0, 0, 0.75);
  padding: 10px 16px;
  outline: none;
}
.form .btn button.fav-btn {
  color: #fff;
  background-color: #A5311E;
  margin-right: 20px;
  width: 196px;
}
.form .btn button.search-btn {
  color: #8B4FB3;
  background: #fff;
}

.continent_card {
  width: 90%;
  text-align: center;
  margin: 70px auto 0 auto;
  border: 5px solid #fff;
  color: #fff;
  transition: 0.2s ease-out;
  cursor: pointer;
}
.continent_card:first-child {
  margin-top: 0;
}
.continent_card figure {
  overflow: hidden;
}
.continent_card figure img {
  width: 100%;
  border-bottom: 5px solid #fff;
  transition: 0.2s ease-out;
}
.continent_card:hover {
  border: 5px solid #FFF30A;
  color: #FFF30A;
}
.continent_card:hover img {
  transform: scale(1.4);
}

.scrollUpBtn {
  display: none;
  position: fixed;
  right: 8px;
  bottom: 8px;
  padding: 8px 10px;
  border: none;
  outline: none;
  box-shadow: -9px 13px 18px 4px rgba(0, 0, 0, 0.75);
  color: #8B4FB3;
  font-size: 1.2rem;
}
.scrollUpBtn.active {
  display: inline-block;
}

.countrie_card {
  width: 90%;
  text-align: center;
  margin: 50px auto 0 auto;
  border: 5px solid #fff;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in;
  /*  */
  max-width: 424px;
  /*  */
}
.countrie_card figure {
  overflow: hidden;
}
.countrie_card figure img {
  width: 100%;
  border-bottom: 5px solid #fff;
  transition: 0.2s ease-in;
}
.countrie_card:hover {
  border: 5px solid #FFF30A;
  color: #FFF30A;
}
.countrie_card:hover img {
  transform: scale(1.4);
}

.countrie-info_card {
  min-height: calc(100vh - 26px);
  background: #8B4FB3;
  font-family: "Kanit", sans-serif;
}
.countrie-info_card img:first-child {
  width: 100%;
}
.countrie-info_card h2 {
  color: #fff;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 30px;
}
.countrie-info_card article {
  margin-left: 5px;
}
.countrie-info_card article p {
  font-size: 1.2rem;
  margin-bottom: 12px;
  color: #fff;
}
.countrie-info_card article p span {
  color: gold;
}
.countrie-info_card article p.timezone {
  display: flex;
  flex-direction: column;
}
.countrie-info_card article p:last-child {
  text-align: center;
  font-size: 1rem;
}
.countrie-info_card article p:last-child a {
  color: #A32243;
}
.countrie-info_card .btn {
  text-align: center;
  margin-top: 30px;
}
.countrie-info_card .btn img {
  width: 20%;
  cursor: pointer;
}

@media screen and (min-width: 900px) {
  .countrie-info_card .info {
    padding: 20px;
    margin-top: 40px;
    display: flex;
    justify-content: space-around;
  }
  .countrie-info_card .info figure {
    flex-grow: 1;
    max-width: 700px;
  }
  .countrie-info_card .info article {
    flex-grow: 8;
    max-width: 300px;
    margin-left: 20px;
    text-align: center;
  }
  .countrie-info_card .info article h2 {
    font-size: 3rem;
  }
  .countrie-info_card .info article p {
    font-size: 1.5rem;
  }
  .countrie-info_card .btn img {
    max-width: 150px;
  }
}
.loading {
  height: calc(100vh - 26px);
  background-color: #8B4FB3;
  display: flex;
  align-items: center;
}
.loading img {
  margin: auto;
  width: 150px;
}

.error {
  height: calc(100vh - 26px);
  background-color: #8B4FB3;
  text-align: center;
}
.error img {
  margin-top: 80px;
  width: 200px;
}
.error h2 {
  width: 95%;
  margin: auto;
  margin-top: 40px;
  color: #fff;
}

.favorite_list {
  min-height: calc(100vh - 26px);
  background-color: #A32243;
  padding: 1px 0 45px 0;
}

@media screen and (min-width: 550px) {
  .favorite_list-content {
    gap: 0 35px;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(380px, 380px));
  }
}
.back_button {
  color: #fff;
  text-align: center;
  width: 30%;
  padding-top: 10px;
  margin-bottom: 20px;
  cursor: pointer;
}
.back_button svg {
  font-size: 1.5rem;
}
.back_button:hover {
  color: #FFF30A;
}/*# sourceMappingURL=styles.css.map */